import { Component, ElementRef, Input, OnChanges, ViewChild, ViewEncapsulation,  HostListener } from '@angular/core';
import * as d3 from 'd3';

@Component({
  selector: 'app-barchart',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './barchart.component.html',
  styleUrls: ['./barchart.component.scss']
})
export class BarChartComponent implements OnChanges {

  @ViewChild('chart', { static: true })

  private chartContainer: ElementRef;


  @Input()
  data: any[] ;



  margin = {top: 50, right: 20, bottom: 50, left: 90};

  constructor() { }

  ngOnChanges(): void {
     if (!this.data) { return; }
     this.createChart();
     
  }

  onResize() {
    this.createChart();
  }

  private createChart(): void {
    d3.select('svg').remove();
    const element = this.chartContainer.nativeElement;
    const data = this.data;

    const svg = d3.select(element).append('svg')
        .attr('width', element.offsetWidth)
        .attr('height', element.offsetHeight);

    const contentWidth = element.offsetWidth - this.margin.left - this.margin.right;
    const contentHeight = element.offsetHeight - this.margin.top - this.margin.bottom;
    const x = d3
  .scaleLinear()
  .rangeRound([0, contentWidth])

 .domain([0, d3.max(data, d => d.frequency)]);
// tslint:disable-next-line:align
const y = d3
  .scaleBand()
  .rangeRound([contentHeight, 0])
  .padding(0.4)
  .domain(data.map(d => d.letter));
    const g = svg.append('g')
      .attr('transform', 'translate(' + this.margin.left + ',' + this.margin.top + ')');
    g.append('g')
   .attr('transform', 'translate(0, ' + contentHeight + ')')
    .call(d3.axisBottom(x));
 // text label for the x axis
    g.append('text')
    .style('color', 'white')
      .attr('transform',
            'translate(' + (contentWidth / 2) + ',' +
                            (contentHeight +30) + ')')
      .style('text-anchor', 'middle')
      .text('Degree of impact');

    g.append('g')
     .call(d3.axisLeft(y));
      g.append('text')     
        .attr('transform', 'rotate(-90)')
        .attr('y', -20)
        .attr('dy', '-3em')
        .style('text-anchor', 'end')
        .style('text-color', 'white')
        .text('Number of Action Items');

    g.selectAll('.bar')
        .data(data)
        .enter()
        .append('rect')
        .attr('class', 'bar')
        // tslint:disable:only-arrow-functions
        .attr('y', function(d) { return y(d.letter); })
        .attr('x', function(d) { return 0; })
        .attr('height', y.bandwidth())
        .attr('width', function(d) { return x(d.frequency); });

  }

}

