import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';
import { BarChartComponent } from './change-impact-component/barchart/barchart.component';

import { ChangeImpactComponentComponent } from './change-impact-component/change-impact-component.component';
import {BubblechartComponent} from './change-impact-component/bubblechart/bubblechart.component';

  const routes: Routes = [
    { path: 'change-impact-component', component: ChangeImpactComponentComponent },
    {
      path: '',
      redirectTo: 'change-impact-component',
      pathMatch: 'full'
    },
    {
      path: '**',
      redirectTo: 'change-impact-component'
    }
];

@NgModule({
  declarations: [ChangeImpactComponentComponent,BarChartComponent, BubblechartComponent],
  
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)],
  exports: [RouterModule,BarChartComponent,BubblechartComponent]
})
export class AppRoutingModule { }
