import { Component, OnInit, ViewChild, ElementRef, ViewEncapsulation, Input } from '@angular/core';
import * as d3 from 'd3';

@Component({
  selector: 'app-bubblechart',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './bubblechart.component.html',
  styleUrls: ['./bubblechart.component.scss']
})
export class BubblechartComponent implements OnInit{
  @ViewChild('bubbleChart', { static: true })
  private chartContainer1: ElementRef;
  @Input()
  bData: any[] ;
   margin = {top: 50, right: 20, bottom: 50, left: 90};

 

  ngOnInit() :void {
    if (!this.bData) { return; }
    this.createBubbleChart();
 }

 onResize() {
   this.createBubbleChart();
 }

 private createBubbleChart(): void {
   d3.select('svg').remove();
   const element1 = this.chartContainer1.nativeElement;
   const data = this.bData;
   const svg = d3.select(element1).append('svg')
   .attr('width', element1.offsetWidth)
   .attr('height', element1.offsetHeight);

const contentWidth = element1.offsetWidth - this.margin.left - this.margin.right;
const contentHeight = element1.offsetHeight - this.margin.top - this.margin.bottom;
 
 const x = d3.scaleLinear()
    .domain([0,  d3.max(this.bData, d => d.BenifitoCh)])
    .range([ 0, contentWidth ]);
  svg.append("g")
    .attr("transform", "translate(0," + contentHeight + ")")
    .call(d3.axisBottom(x));

  // Add Y axis
  const y = d3.scaleLinear()
    .domain([d3.max(this.bData, d => d.BenifitoCh), 0])
    .range([ contentHeight, 0]);
  svg.append("g")
    .call(d3.axisLeft(y));

  // Add a scale for bubble size
  var z = d3.scaleLinear()
    // .domain([10, 50])
    // .range([ 1, 40]);

  // Add dots
  svg.append('g')
    .selectAll("dot")
    .data(this.bData)
    .enter()
    .append("circle")
      .attr("cx", function (d) { console.log(d.BenifitoCh); return x(d.BenifitoCh); } )
      .attr("cy", function (d) { return y(d.ImpactofCha); } )
      .attr("r", function (d) { return z(d.NoofRoles); } )
      .style("fill", "#69b3a2")
      .style("opacity", "0.7")
      .attr("stroke", "black")
      
}


}
