import { Component, OnInit } from '@angular/core';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-change-impact-component',
  templateUrl: './change-impact-component.component.html',
  styleUrls: ['./change-impact-component.component.scss']
})
export class ChangeImpactComponentComponent implements OnInit {
  public innerWidth: any;

  ngOnInit():void {

  }
  storeData: any;
  csvData: any;
  jsonData: any;
  textData: any;
  htmlData: any;
  fileUploaded: File;
  worksheet: any;
 
  barData = [];
  PerData = [];
  projectPhases = [];
  targetTeams = [];
  changeImpactLogDetails = [];
  barchartrender=[];
  bubblechatrender=[];
  bubbleData=[];
  public display:boolean;

  loadComponent: any;
  uploadedFile(event) {
    this.fileUploaded = event.target.files[0];
  }
  Upload() {
  this.readExcel();
  this.loadComponent=true;
  this.display=true;
  }
  readExcel() {
    const readFile = new FileReader();
    readFile.onload = (e) => {
      this.storeData = readFile.result;
      const data = new Uint8Array(this.storeData);
      const arr = new Array();
      for (let i = 0; i !== data.length; ++i) { arr[i] = String.fromCharCode(data[i]); }
      const bstr = arr.join('');
      const workbook = XLSX.read(bstr, { type: 'binary' });
      // tslint:disable-next-line:variable-name
      this.worksheet = workbook.Sheets['Data Mapping'];
      // To fetch bar chart data
      for (let j = 5; j <= 7; j++) {
        const indices = ['E' + j, 'F' + j];
        // tslint:disable-next-line:object-literal-key-quotes
      //  this.barData[j - 5] = {'letter':  this.worksheet[indices[0]].v, 'frequency':  this.worksheet[indices[1]].v};
      }
      this.barchartrender[0] = {"barchart":"rend"};

      
      // To fetch circle data
      for (let k = 10; k <= 11; k++) {
        const indices = ['B' + k, 'E' + k, 'F' + k];
        // const colors = ['orangered', 'green'];
        // // tslint:disable:object-literal-key-quotes
        // this.PerData[k - 10] = {'status':  this.worksheet[indices[0]].v,
        // 'value':  this.worksheet[indices[1]].v, 'percentage': Math.round((this.worksheet[indices[2]].v) * 100), 'color': colors[k-10]};
      }
    /* loop to fetch data for project phases and target teams  */
      const worksheet1 = workbook.Sheets['Change Impacts'];
      let highCount =0;
      let medCount  =0;
      let lowCount =0;

      for(let a=2;a<=10;a++){
        
        const indices=['D'+a];
        if(worksheet1[indices[0]] !== undefined){
          if( worksheet1[indices[0]].v == "High"){
            highCount++;

          }
          else if( worksheet1[indices[0]].v == "Medium"){
            medCount++;

          }
          else if( worksheet1[indices[0]].v == "Low"){
            lowCount++;
            
          }
        }

      }
      this.barData[2]={'letter':  'High', 'frequency':  highCount};
      this.barData[1]={'letter':  'Medium', 'frequency':  medCount};
      this.barData[0]={'letter':  'Low', 'frequency':  lowCount};

      let inCompCount =0;
      let inProCount  =0;
      let inDelCount =0;
      let  totalCount=0;
      for(let a=2;a<=10;a++){
        
        const indices=['J'+a];
        if(worksheet1[indices[0]] !== undefined){
         // console.log("status"+worksheet1[indices[0]].v);
          if( worksheet1[indices[0]].v >=0.85){
            inCompCount++;

          }
          else if( worksheet1[indices[0]].v >= 0.70 && worksheet1[indices[0]].v < 0.85){
            inProCount++;

          }
          else if( worksheet1[indices[0]].v < 0.70){
            inDelCount++;
            
          }
        }
        
      }
      totalCount=inCompCount+inProCount+inDelCount;
      let proPercntaga=(inProCount/totalCount)*100;
      let ComPercntaga=(inCompCount/totalCount)*100;
      console.log("proPercntaga"+proPercntaga +"ComPercntaga"+ComPercntaga);
      const colors = ['orangered', 'green'];
    
      this.PerData[0]={'status':  'Completed', 'value':  inCompCount,'percentage':ComPercntaga,'color':colors[0]};
      this.PerData[1]={'status':  'In Progress', 'value':  inProCount,'percentage':proPercntaga,'color':colors[1]};






      for (let a = 2; a <= 10; a++) {
          const indices = ['M' + a , 'N' + a];
          if (worksheet1[indices[0]] !== undefined ) {
            this.projectPhases[a - 2] = { 'projectPhases' : worksheet1[indices[0]].v};
          }
          if (worksheet1[indices[1]] !== undefined && worksheet1[indices[1]] !== '') {
            this.targetTeams[a - 2] = { 'targetTeams' : worksheet1[indices[1]].v};
          }
      }
      /* loop to fetch data for table */
      for (let b = 1; b <= 10; b++) {
        const indices = ['C' + b , 'D' + b , 'E' + b , 'F' + b , 'G' + b , 'H' + b , 'I' + b , 'J' + b ];
        if (worksheet1[indices[1]] !== undefined ) {
            this.changeImpactLogDetails[b - 1] = { 'Impact' : worksheet1[indices[0]].v , 'DegreeofImpact' : worksheet1[indices[1]].v,
            'Regions' : worksheet1[indices[2]].v , 'ImpactedStackholder' : worksheet1[indices[3]].v,
            'ChangeIntervention' : worksheet1[indices[4]].v, 'Frequency' : worksheet1[indices[5]].v,
            'Assignedto' : worksheet1[indices[6]].v, 'Status' : Math.round((worksheet1[indices[7]].v) * 100)
          };
        }
      }
      const worksheet2 = workbook.Sheets['Change HeatMap Data'];
      for (let b = 3; b <= 8; b++) {
        const indices = ['A' + b , 'B' + b , 'C' + b , 'D' + b ];
        if (worksheet2[indices[1]] !== undefined ) {
            this.bubbleData[b - 3] = { 'Change' : worksheet2[indices[0]].v , 'BenifitoCh' : worksheet2[indices[1]].v,
            'ImpactofCha' : worksheet2[indices[2]].v, 'NoofRoles ' : worksheet2[indices[3]].v };
        }
      }



      
     // console.log(JSON.stringify(this.bubbleData));
    };
    readFile.readAsArrayBuffer(this.fileUploaded);
  }

}
